.sidebar-container{display:none;position:fixed;height:150px;width:100%;z-index:99}.sidebar-container label{position:absolute;width:2.4rem;height:0.3rem;right:1rem;top:50%;background-color:#fff;cursor:pointer;z-index:100}.sidebar-container label::before,.sidebar-container label::after{content:"";position:absolute;display:block;width:2.4rem;height:0.3rem;background-color:#fff;transition:all 0.4s cubic-bezier(1, 0, 0, 1)}.sidebar-container label::before{margin-top:-25%;-webkit-transform-origin:left top;transform-origin:left top;-webkit-transform:rotateZ(0);transform:rotateZ(0)}.sidebar-container label::after{margin-top:27%;-webkit-transform-origin:left bottom;transform-origin:left bottom;-webkit-transform:rotateZ(0);transform:rotateZ(0)}.sidebar-container #check{display:none}.sidebar-container #check:checked ~ label::before{width:2rem;background:white;-webkit-transform:rotateZ(41deg);transform:rotateZ(41deg)}.sidebar-container #check:checked ~ label::after{width:2rem;background:white;-webkit-transform:rotateZ(-41deg);transform:rotateZ(-41deg)}.sidebar-container #check:checked ~ label{background-color:transparent}.sidebar-container #check:checked ~ .sidebar-content{-webkit-transform:translateX(0px);transform:translateX(0px)}.sidebar-container .sidebar-content{position:fixed;top:0;right:0;width:250px;height:100%;-webkit-transform:translateX(2500px);transform:translateX(2500px);background-color:#333;z-index:99;transition:all 1s cubic-bezier(0.25, 0.46, 0.45, 0.94) both}.sidebar-container .sidebar-content .options{margin-top:80px;height:300px;display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-align-items:center;align-items:center;-webkit-justify-content:space-between;justify-content:space-between}.sidebar-container .sidebar-content .options a{position:relative;cursor:pointer;text-transform:uppercase}.sidebar-container .sidebar-content .options a::before{content:"";position:absolute;width:0;height:3px;right:0;top:-10px;border-radius:10px;transition:all 500ms ease;background-color:white}.sidebar-container .sidebar-content .options a::after{content:"";position:absolute;width:0;height:3px;left:0;bottom:-10px;background-color:white;border-radius:10px;transition:all 500ms ease}.sidebar-container .sidebar-content .options a:hover::after,.sidebar-container .sidebar-content .options a:hover::before,.sidebar-container .sidebar-content .options a.active::before,.sidebar-container .sidebar-content .options a.active::after{width:100%}@media screen and (max-width: 775px){.sidebar-container{display:block}}

